ul#all-posts {
    list-style-type: none;
    margin-top: 32px;
    padding: 0;

    & li {
        color: #aaa;
        margin-bottom: 25px;

        & button {
            background-color: transparent;
            border: 0;
            margin-top: 5px;
            padding: 0;
            text-decoration: underline;

            &:hover {
                color: #fff;
            }
        }
    }
}
