#post {
    & #topic {
        width: 100%;
        height: 85px;
        box-sizing: border-box;
    }

    & div[contenteditable="true"] {
        width: 100%;
        box-sizing: border-box;
        overflow: auto;
        text-align: left;

        &#post {
            height: 500px;
        }

        &#tweet {
            height: 150px;
        }

        & .revision {
            & s {
                color: lightsalmon;
                font-style: italic;
            }

            & b {
                color: lightgreen;
            }
        }
    }

    .char-count {
        font-weight: bold;

        &.valid {
            color: lightgreen;
        }

        &.invalid {
            color: lightsalmon;
        }
    }

    label.revise-label {
        & span {
            text-align: left;
        }
    }

    & .accept-reject {
        display: none;
    }

    & #post-meta-data {
        & button {
            width: fit-content;
        }

        & input {
            width: 100%;
            box-sizing: border-box;
        }
    }
}
