/* UTILITY CLASSES */

& .flex {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;

    &.column {
        flex-direction: column;
    }
}

/* END UTILITY CLASSES */

body {
    background-color: #333;
    color: #eee;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    margin: 50px 15px 80px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a {
    color: lightblue;
}

hr {
    height: 2.75px;
    width: 100%;
    background-color: #555;
    border: 0;
}

button,
& div[contenteditable="true"],
h1,
h2,
hr,
input,
select,
textarea {
    margin: 0;
}

body,
button,
& div[contenteditable="true"],
input,
select,
textarea {
    appearance: none;
    color: #eee;
    font-family: "Courier New", Courier, monospace;
    font-size: 1rem;
}

button,
& div[contenteditable="true"],
input,
select,
textarea {
    border-radius: 5px;
}

& div[contenteditable="true"],
input,
select,
textarea {
    background-color: #444;
    border: 2px solid lightblue;
    padding: 3px;
}

button,
input[type="checkbox"] {
    &:hover {
        background-color: #666;
        cursor: pointer;
    }
}

button {
    background-color: #222;
    border: 2px solid #111;
    color: #ccc;
    font-weight: bold;
    padding: 3px 10px;
}

input[type="checkbox"] {
    height: 20px;
    width: 20px;
    padding: 0;

    &:checked {
        &::before {
            content: "X";
            display: flex;
            align-items: center;
            justify-content: center;
            font-weight: bold;
        }
    }
}
